.footer {
    padding-top: 30px;
    padding-bottom: 65px;
    .footer__wrapper {
        display: flex;
        justify-content: space-between;

        .footer__text {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 300;
            font-size: 18px;
            line-height: 139.02%;
            margin-bottom: 0;
            display: flex;
            display: inline-block;
            color: #6A6A6A;
        }

        .footer__img {
            width: 120px;
            height: 53px;
        }

        .footer__lang {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 28.8px;
            line-height: 113.52%;
            display: flex;
            text-transform: uppercase;
            align-items: center;
            color: #0B0B0C;
        }
    }
}