.header {
    min-height: 92px;
    height: 100%;
}
.header__nav { 
    padding-top: 36px;
    padding-bottom: 36px;
    
}
.header__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 400px;
    
    
}
.header__img-wrapper {
    .header__img {
        width: 50px;
        height: 20px;
    }
}

.header__center {
    display: flex;
    gap: 286px;
    
}
.header__list {
    list-style-type: none;
    text-decoration: none;
    display: flex;
    margin: 0;
    padding: 0;
    
    
}
.header__item {
    list-style-type: none;
    text-decoration: none;
    
}

.header__right {
    display: flex;
}

.header__link {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 113.52%;
    color: #0B0B0C;
    list-style-type: none;
    text-decoration: none;
    flex-wrap: nowrap;
    padding-bottom: 3px;
    transition: all 0.4s ease;
    &:hover {
        box-shadow: 0 1.5px #000000;
    }
}