// TEXT

.text {
    margin-top: 200px;
}

.text__wrapepr {
    display: flex;
    
}

.text__wrapper {
    display: flex;
    align-items: center;
    gap: 235px;
    user-select: none;
    -webkit-user-select: none;
}

.text__img-wrapper {
    display: flex;
    flex-direction: column;
    gap: 261px;
}

.text__header {
    margin: 0;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 300;
    font-size: 116px;
    line-height: 122.5%;
    color: #0B0B0C;
    max-width: 1105px;
    width: 100%;
    
    align-items: center;
}

// ITEM

.item {
    padding-top: 186px;
    
    .item__wrapper {
        user-select: none;
        -webkit-user-select: none;
        
        .item__span {
            border-bottom: 1.5px solid #000000;
            width: 100%;
            background-color: #fff;
            height: 1px;
            color: #000000;
            padding-top: 16px;
            display: inline-block;
            margin-bottom: 20px;
        }
        
        .item__text {
            margin: 0;
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 113.52%;
            text-transform: uppercase;
            color: #000000;
            margin-left: auto;
            margin-right: auto;
        }
        
        .item__list {
            list-style-type: none;
            text-decoration: none;
            margin: 0;
            padding-left: 0;
            display: flex;
            flex-wrap: wrap;
            gap: 12px;
            justify-content: space-between;
            align-items: center;
            .item__item {
                background-color: #FFFFFF;
                border: 1.5px solid #0B0B0C;
                border-radius: 8px;
                width: 204px;
                align-items: center;
                display: flex;
                padding-top: 15px;
                padding-bottom: 15px;
                transition: all 0.4s ease;
                
                &:hover {
                    
                    .item__text {
                        color: #FFFFFF;
                    }
                    
                    background-color: #0B0B0C;
                    transform: rotate(-5deg);
                    
                }
                
                
            }
        }
        
    }
}

// IMG

.img {
    padding-top: 42px;
    .img__wrapper{
        user-select: none;
        -webkit-user-select: none;
        .img__img-top{
            width: 1770px;
            height: 761px;
        }
        .img__text {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 28.8px;
            line-height: 113.52%;
            color: #0B0B0C;
            padding-bottom: 3px;
            display: inline-block;
            margin: 0;
            transition: all 0.4s ease;
            margin-top: 4px;
            &:hover {
                box-shadow: 0 1.5px #000000;
            }
        }
        
        .img__list {
            display: flex;
            list-style-type: none;
            text-decoration: none;
            width: 1886px;
            gap: 20px;
            flex-wrap: wrap;
            margin: 0;
            padding: 0;
            
            .img__item {
                display: flex;
                flex-direction: column;
                .img__img {
                    display: block;
                }
            }
        }
    }
}

// INFO

.info {
    padding-top: 164px;
    padding-bottom: 114px;
    
    .info__wrapper {
        display: flex;
        .info__left {
            max-width: 875px;
            width: 100%;
            display: flex;
            flex-direction: column;
            .info__text {
                font-family: 'Inter';
                font-size: 18px;
                line-height: 139.02%;
                color: #000000;
                width: 440px;
                margin-bottom: 19px; 
                display: inline-block;
            }
            
            .info__link {
                padding: 15px 59px;
                font-family: 'Inter';
                font-size: 18px;
                line-height: 113.52%;
                color: #0B0B0C;
                background-color: #FFFFFF;
                border: 1.5px solid #0B0B0C;
                border-radius: 8px;
                list-style-type: none;
                text-decoration: none;
                width: 89px;
                height: 20px;
                display: inline-block;
            }
        }
        .info__text-l{
            display: inline;
            margin: 0;
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 28.8px;
            line-height: 113.52%;
            align-items: flex-start;
            color: #7A7A7A;
            margin-top: 20px;
            margin-right: 4px;
            margin-left: 20px;
        }
        
        .info__right {
            display: flex;
            align-items: center;
          
            .info__r-wrapper {
                display: flex;
                .info__span {
                    display: inline-block;
                    height: 2px;
                    width: 273px;
                    background-color: #000000;
                    margin-top: 43px;
                    margin-right: 5px;
                }   
                
                .info__list {
                    list-style-type: none;
                    text-decoration: none;
                    margin: 0;
                    padding: 0;
                    display: flex;
                    flex-direction: column;
                    text-align: right;
                    
                    .info__item {
                        display: inline-block;
                        .info__link {
                            list-style-type: none;
                            text-decoration: none;
                            font-family: 'Inter';
                            font-size: 28.8px;
                            line-height: 174.52%;
                            text-align: right;
                            color: #000000;
                            transition: all 0.4s ease;
                            &:hover {
                                box-shadow: 0 1.5px #000000;
                            }
                        }
                    }
                }
            }
            
            
        }
        
        
    }
}